	module.exports = {"data":{"markdownRemark":{"html":"<h1>Contributing</h1>\n<p>The process for contributing code to Kubernetes via SIG-aws <a href=\"/sig-aws\">community</a>.</p>\n<p><strong>Note</strong>: This page is focused on helping new contributors become active\nmembers of the community through sustained contributions. </p>\n<p>If you are interested in contributing to Kubernetes as a whole there is a top level\n<a href=\"/contributors/guide\">contributor's guide</a>. </p>\n<h2>Introduction</h2>\n<p>Welcome to the Kubernetes sig-aws contributing guide.  We are excited\nabout the prospect of you joining our <a href=\"/sig-aws\">community</a>!\nMentoring and on-boarding new contributors is critical to the success\nof the project.</p>\n<p>Please be aware that all contributions to Kubernetes require time\nand commitment from project maintainers to direct and review work.\nThis is done in additional to many other maintainer responsibilities, and\ndirect engagement from maintainers is a finite resource.</p>\n<h2>Before You Begin</h2>\n<p><strong>Note</strong>: Complete the following steps before reaching out to aws community members.</p>\n<h3>Agree to contribution rules</h3>\n<p>Follow the <a href=\"../CLA.md\">CLA signup instructions</a>.</p>\n<h3>Understand the big picture</h3>\n<p>This is important.</p>\n<ul>\n<li>Play around with Kubernetes <a href=\"https://kubernetes.io/docs/tutorials/kubernetes-basics\">Kubernetes Basics Tutorial</a>.</li>\n<li>Get to know possibilities to set up Kubernetes on AWS <a href=\"https://kubernetes.io/docs/getting-started-guides/aws/\">https://kubernetes.io/docs/getting-started-guides/aws/</a></li>\n<li>Understand how Kubernetes on aws differs from other installations of Kubernetes [/contributors/design-proposals/aws/aws<em>under</em>the_hood.md]</li>\n</ul>\n<h2>Adopt an issue</h2>\n<p>New contributors can try the following to work on an existing bug or approved design:</p>\n<ul>\n<li>\n<p>In <a href=\"https://kubernetes.slack.com/messages/sig-aws\">slack</a> (signup <a href=\"http://slack.k8s.io/\">here</a>),\n@mention a <a href=\"/sig-aws#leads\">lead</a> and ask if there are any issues you could pick up.\nWe also maintain a list of <a href=\"https://github.com/kubernetes/kops/issues?utf8=%E2%9C%93&#x26;q=is%3Aopen+is%3Aissue+label%3Agood-starter-issue\">sig aws issues where help is wanted</a>.\nMost of them are not very complex, so that's probably a good starting point.\nLeads can recommend issues that have enough priority to receive PR review bandwidth.</p>\n</li>\n<li>\n<p>Send an email to the <em>kubernetes-sig-aws@googlegroups.com</em> <a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-aws\">group</a></p>\n<blockquote>\n<p>Subject: New sig-aws contributor <em>${yourName}</em></p>\n<p>Body: Hello, my name is <em>${yourName}</em>.  I would like to get involved in\ncontributing to the Kubernetes project.  I have read all of the\nuser documentation listed on the community contributing page.\nWhat should I do next to get started?</p>\n</blockquote>\n</li>\n<li>\n<p>Attend a sig-aws <a href=\"/sig-aws#meetings\">meeting</a> and introduce yourself as looking to get started.</p>\n</li>\n</ul>\n<h2>Meet the community</h2>\n<p>Engage with the SIG aws community!  Let us know who you are and how things are going!</p>\n<ul>\n<li>\n<p>In <a href=\"https://kubernetes.slack.com/messages/sig-aws\">slack</a> (signup <a href=\"http://slack.k8s.io/\">here</a>),\n@mention a <a href=\"/sig-aws#leads\">lead</a> and ask if there are any issues you could pick up, or\nlet them know what you are working on.</p>\n</li>\n<li>\n<p>Attend a sig-aws <a href=\"/sig-aws#meetings\">meeting</a> and introduce yourself and what you are working on.</p>\n</li>\n<li>\n<p>The sig-aws <a href=\"/sig-aws\">community page</a> lists sig-aws <a href=\"/sig-aws#leads\">leads</a>, channels of <a href=\"/sig-aws#contact\">communication</a>,\nand group <a href=\"/sig-aws#meetings\">meeting</a> times.</p>\n</li>\n</ul>"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-aws/contributing/"}}

